 .widget__contents{
     display: flex;
     flex-direction: column; 
     cursor: pointer;
 }

 .widget__content{
     margin: 5px; 
     display: flex;
     padding: 5px;
     border-bottom: 1px solid lightgray;

 } 

 .widget__content > img {
     margin-top:15px;
     height:50px;
     width:50px;
     border-radius: 50%;
     
 }

 .widget__contentTitle{
     margin-left: 10px;
 }

 .widget__contentTitle > h5{
    color:rgb(70,68,68);
}

.widget__contentTitle > p {
    color: gray;
    font-size: small;
}